.login {
	background-color: white;
	height: 100vh;
	display: flex;
	flex-direction: column;
	align-items: center;
}
.login__container {
	width: 300px;
	display: flex;
	flex-direction: column;
	padding: 20px;
	border: 1px solid lightgray

}
.login__container > h1 {
	font-weight: 500;
	margin-bottom: 20px;
}
.login__container >form > h5 {
	margin-bottom: 5px;
}
.login__container > form > input {
	height: 30px;
	margin-bottom: 10px;
	background-color: white;
	width: 98%;
}
.login__contaienr > p {
	margin-top: 15px;
	font-size: 12px;
}
.login__singInButton {
	background: #f0c14b;
	border-radius: 2px;
	width: 100%;
	height: 30px;
	border: 1px solid;
	margin-top: 10px;
	border-color: #a88734 #9c7e31 #846a29;
}
.login__registerButton{
	border-radius: 2px;
	width: 100%;
	height: 30px;
	border: 1px solid;
	margin-top: 10px;
	border-color: darkgray;
}
.login__logo {
	margin: 20px 0;
	width: 100px;
	object-fit: contain;
}